}
@media (max-width: 767px) {
  .programs__slide-button {
    padding-left: 0;
    margin-left: auto;
  }
}
.programs__slide-button:hover {
  background-color: transparent;
}
.programs__slide-button:hover:active {
  background-color: transparent;
}
.programs__slide-button:active {
  background-color: transparent;
}

.programs__button-icon {
  width: 30px;
  height: 30px;
  padding: 0;
}
@media (max-width: 767px) {
  .programs__button-icon {
    width: 30px;
    height: 30px;
    padding: 0;
  }
}

.programs__slider-footer {
  grid-column: 1/-1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 34px;
