@media (max-width: 1199px) {
  .programs__slider-buttons {
    margin-right: 45px;
  }
}
@media (max-width: 767px) {
  .programs__slider-buttons {
    margin-left: auto;
    margin-right: 0;
    gap: 7px;
    transform: none;
  }
