.instruction {
  padding-top: 125px;
  padding-bottom: 107px;
  color: #ffffff;
  background-color: #316dc2;
}
@media (max-width: 1199px) {
  .instruction {
    padding-top: 98px;
    padding-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .instruction {
    padding-top: 69px;
    padding-bottom: 68px;
  }
}

.instruction__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 34px;
}
@media (max-width: 1199px) {
  .instruction__wrapper {
    display: flex;
    flex-direction: column;
    gap: 46px;
  }
}
@media (max-width: 767px) {
  .instruction__wrapper {
    gap: 34px;
  }
}

.instruction__location {
  width: 89%;
}
@media (max-width: 1199px) {
  .instruction__location {
    width: 100%;
  }
}
.instruction__location h2 {
  margin-bottom: 28px;
}
@media (max-width: 1199px) {
  .instruction__location h2 {
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .instruction__location h2 {
    margin-bottom: 13px;
  }
}
.instruction__location p {
  margin: 0;
  margin-bottom: 33px;
  font-size: 16px;
  line-height: 26px;
}
@media (max-width: 1199px) {
  .instruction__location p {
    width: 75%;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .instruction__location p {
    width: 100%;
    margin-bottom: 26px;
    font-size: 14px;
    line-height: 20px;
  }
}
.instruction__location picture,
.instruction__location img {
  width: 100%;
  max-width: 498px;
  height: auto;
  object-fit: contain;
  border-radius: 16px;
}
@media (max-width: 1199px) {
  .instruction__location picture,
.instruction__location img {
    max-width: 100%;
  }
}

.instruction__grant {
  width: 82%;
}
@media (max-width: 1199px) {
  .instruction__grant {
    width: 100%;
  }
}
.instruction__grant h3 {
  margin: 0;
  margin-bottom: 55px;
  font-size: 32px;
  font-weight: 400;
  line-height: 38px;
}
@media (max-width: 1199px) {
  .instruction__grant h3 {
    margin-bottom: 41px;
    font-size: 28px;
    line-height: 28px;
  }
}
@media (max-width: 767px) {
  .instruction__grant h3 {
    margin-bottom: 27px;
    font-size: 18px;
    line-height: 22px;
  }
}
.instruction__grant p {
  margin: 0;
  font-size: 16px;
  line-height: 26px;
}
@media (max-width: 1199px) {
  .instruction__grant p {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .instruction__grant p {
    font-size: 14px;
    line-height: 20px;
  }
}

.instruction__list {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  gap: 23px;
  list-style: none;
  counter-reset: section;
}
@media (max-width: 1199px) {
  .instruction__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 47px;
    row-gap: 25px;
  }
}
@media (max-width: 767px) {
  .instruction__list {
    display: flex;
    flex-direction: column;
    padding-left: 16px;
    row-gap: 16px;
  }
}

.instruction__item {
  position: relative;
  padding: 23px 72px 23px 25px;
  outline: 1px solid #f0efe9;
  border-radius: 16px;
}
.instruction__item::before {
  position: absolute;
  top: -15px;
  left: -15px;
  padding: 10px;
  width: 42px;
  height: 42px;
  font-size: 22px;
  line-height: 22px;
  background-color: #316dc2;
  counter-increment: section;
  content: counter(section);
}
@media (max-width: 767px) {
  .instruction__item::before {
    top: -7px;
    left: -16px;
    padding: 10px 4px;
    width: 30px;
    height: 32px;
    font-size: 18px;
    line-height: 13px;
    text-align: center;
  }
}
@media (max-width: 1199px) {
  .instruction__item {
    padding: 24px 23px 28px 25px;
  }
}
@media (max-width: 767px) {
  .instruction__item {
    padding: 18px 23px 19px 25px;
  }
}

