.about {
  padding-top: 125px;

  @include vp-1199 {
    padding-top: 99px;
  }

  @include vp-767 {
    padding-top: 68px;
  }
}

.about__flex-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 34px;
  margin-bottom: 60px;

  @include vp-1199 {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 73%;
    margin-bottom: 62px;
  }

  @include vp-767 {
    width: 100%;
    margin-bottom: 39px;
  }
}

.about__info {
  width: 90%;

  h2 {
    margin-bottom: 27px;

    @include vp-1199 {
      margin-bottom: 26px;
    }

    @include vp-767 {
      margin-bottom: 14px;
    }
  }

  p {
    margin: 0;
    font-size: 22px;
    line-height: 32px;

    @include vp-1199 {
      font-size: 20px;
      line-height: 26px;
    }

    @include vp-767 {
      width: 98%;
      font-size: 16px;
      line-height: 22px;
    }
  }
}

.about__goal {
  width: 85%;

  @include vp-1199 {
    width: 100%;
  }

  h3 {
    margin: 0;
    margin-bottom: 38px;
    font-size: 32px;
    line-height: 24px;
    font-weight: 400;
    color: $color-cerulean-blue;

    @include vp-1199 {
      font-size: 28px;
      line-height: 28px;
      margin-bottom: 22px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 10px;
    }
  }

  p {
    margin: 0;
    margin-bottom: 33px;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;

    @include vp-1199 {
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 25px;
    }
  }
}

.about__button {
  width: fit-content;
  min-width: 277px;

  @include vp-767 {
    min-width: auto;
  }
}

.about__advantages {
  color: $color-default-white;
  background-color: $color-cerulean-blue;
  padding: 92px 106px 58px 127px;
  border-radius: 20px;

  @include vp-1199 {
    width: 100%;
    padding: 62px 40px 40px 60px;
  }

  @include vp-767 {
    padding: 42px 20px 42px 30px;
  }
}


.about__advantages-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  row-gap: 52px;
  column-gap: 52px;

  @include vp-1199 {
    row-gap: 42px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 23px;
  }
}

.about__advantages-item {
  position: relative;

  height: 100%;
  padding: 30px 60px;
  display: flex;
  gap: 20px;
  justify-content: space-between;

  outline: 1px solid $color-alabaster;
  border-radius: 20px;


  @include vp-1199 {
    display: flex;
    flex-direction: column;
    gap: 9px;
    padding: 30px 30px 25px;
  }

  @include vp-767 {
    padding: 21px 18px 21px 20px;
    gap: 5px;
  }

  @media (min-width: 350px) and (max-width: 767px) {
    align-items: center;

    p,
    h3 {
      text-align: center;
    }
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    z-index: 1;
    left: -21px;
    top: -21px;

    width: 42px;
    height: 42px;
    padding: 10px;

    background-image: url("../img/svg/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-color: $color-cerulean-blue;

    @include vp-767 {
      background-size: 18px 18px;
    }
  }

  h3 {
    margin: 0;
    width: fit-content;

    font-size: 26px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0.26px;
    text-transform: uppercase;

    @include vp-1199 {
      font-size: 20px;
      line-height: 22px;
      letter-spacing: 0.2px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 20px;
      letter-spacing: 0.16px;
    }
  }

  p {
    margin: 0;
    width: fit-content;

    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    color: $color-alabaster;

    @include vp-1199 {
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
